import React, { Component } from 'react';
import Form from './styles/Form';

class SignupForm extends Component {
    render() {
        return (
          <Form
            data-test="form"
            onSubmit={ e => {
                e.preventDefault();
            }}
          >
          <fieldset>
              <label htmlFor="email"></label>
          </fieldset>
              
          </Form>
        );
    }
}

export default SignupForm;